import React ,{FC,useEffect}from 'react';
import { BrowserRouter } from 'react-router-dom';
import Greeting from "./pages/Greeting"
import Home from "./pages/Home"
import Auth from "./auth0/Auth"
import * as PusherPushNotifications from "@pusher/push-notifications-web"
const App:FC<any> = (props:any) => {
    const auth = new Auth(window.history)

    useEffect(()=>{
        console.log("helllo")
        PusherPushNotifications.init({
            instanceId: 'a2f50c2f-9c1f-49df-8d75-199fb0cd8671',
          })
            .then((beamsClient:any) =>
              beamsClient
                .start()
                .then(()=>console.log("hellloooo"))
                .then(() => beamsClient.addDeviceInterest('hello'))
                .then(() => console.log('Successfully registered and subscribed! Device ID:', beamsClient.deviceId)),
            )
            .catch(console.error);
    })

     return (
        <div>
            <BrowserRouter>
                <Greeting auth={auth} {...props}/>
                <Home auth={auth}/>
            </BrowserRouter>
        </div>
    );
}

export default App;
